.AsteroidBackgroud{
    margin: 10px 0;
    border: 1px solid black;
    border-radius: 10px;
}
.DangerousAsteroidBakcground{
    background: linear-gradient(to right, #FFB199, #FF0844);

}
.SaftiAsteroidBakcground{
    background: linear-gradient(to right, #CFF37D, #7DE88C);

}
.AsteroidPage{
    margin: 10px 10% 0 10%;
}
.AsteroidBox{
    background-image: url("../Frame 2.svg");
    background-repeat: no-repeat;
    background-position: 15% 10%;
    display: grid;
    grid-template-rows: 1fr;
    grid-template-columns: 1fr 1fr 1fr;
}
.asteroidInfo{
}
.destroyedBox{
    text-align: center;
}
.AsteroidWithOption{
    display: grid;
    grid-template-columns: auto 1fr auto;
    grid-template-rows: auto 1fr;
    grid-template-areas: "Checkbox . sortByDistanse"
                        "asteroidField asteroidField asteroidField";
}
.sortByDistanseContainer{
    grid-area: sortByDistanse;
    display: grid;
    grid-template-rows: auto;
    grid-template-columns: auto auto auto;
    grid-gap: 0 5px;
}
.sortByDistanseContainer p{
    margin: 0;
}
.ShowDangerousCheckbox{
    grid-area: Checkbox;
}
.asteroidsField{
    grid-area: asteroidField;
}
.imageBox{
    position: relative;
}
.btnToDestroyAsteroid{
    padding: 10px;
    border-radius: 20px;
    background: #186DD6;
    color: white;
    transition: all 0.3s;
    border: none;
}
.btnToDestroyAsteroidSelecter{
    padding: 10px;
    border-radius: 20px;
    background: white;
    color: #186DD6;
    transition: all 0.3s;
    border: 2px solid #186DD6;
}
.btnToDestroyAsteroid:focus,
.btnToDestroyAsteroidSelecter:focus{
    outline: none;
}
.btnToDestroyAsteroid:active,
.btnToDestroyAsteroidSelecter:active{
    position: relative;
    top: 3px;
}
.btnToDestroyAsteroidSelecter::after{
    position: absolute;
}
.btnToDestroyAsteroidSelecter:hover{

}

.imageBox img{
    position: absolute;
    bottom: 0;
    left: 2%;
}



.testBorder{
    display: grid;
    grid-auto-rows: 1fr;
    grid-template-columns: auto 1fr auto;
    grid-template-areas: "test1 test2 test3";
}
.test2{
    top: -18%;
    position: relative;
    border-bottom: 1px dotted black;
    grid-area: test2;
}
.test1{
    grid-area: test1;
}
.test3{
    grid-area: test3;
}
.toogleSortByDistanceBtn{
}


.toogleSortByDistanceBtn.active{
    font-weight: 700;
}
.sortByDistanse{
    display: none;
}
.sortByDistanseBox input ~ label{
    text-decoration: underline;
}
.sortByDistanseBox input:checked ~ label{
    font-weight: 700;
    text-decoration: none;
}
.asteroidName{
    color: black;
    font-size: 1.5rem;
    text-align: left;
    font-weight: 700;
    display: block;
}
@media (max-width: 500px) {
    .AsteroidWithOption{
        grid-template-columns:  1fr ;
        grid-template-rows: auto 1fr;
        grid-template-areas: "Checkbox"
                            "sortByDistanse"
                            "asteroidField ";
    }
    .ShowDangerousCheckbox{
        text-align: left;
    }
    .sortByDistanseContainer{
        grid-area: sortByDistanse;
        display: block;
        text-align: left;
    }
    .sortByDistanseBox{
        display: inline-block;
    }
    .sortByDistanseContainer p{
        display: inline-block;
    }
    .AsteroidBox{
        grid-template-rows: 1fr 1fr 1fr;
        grid-template-columns: 1fr;
    }
    .asteroidInfo{
        background: white;
        padding: 0 10px;
        position: relative;
    }
    .destroyedBox{
        background: white;
        border-radius: 0 0 10px 10px;
    }
    .imageBox img{
        position: absolute;
        bottom: -10%;
        left: 78%;
        transform: rotate3d(0, 40, 0, 180deg) scale(0.6);
    }
    .asteroidName{
        position: absolute;
        top: -36%;
        left: 3%;
        font-size: 20px;
        font-weight: 700;
    }

}

